<template>
  <b-card
    class="blog-wrapper"
  >
    <!-- form -->
    <b-form class="mt-2">
      <b-row>
        <b-col
          cols="12"
          class="mb-2"
        >
          <div class="border rounded p-2">
            <h4 class="mb-1">
              Featured Image
            </h4>
            <validation-provider
              #default="{ errors }"
              name="Image"
              rules="required"
            >
              <b-media
                no-body
                vertical-align="center"
                class="flex-column flex-md-row"
              >
                <!-- <b-media-aside>
                  <b-img
                    ref="refPreviewEl"
                    :src="news.file.url"
                    height="110"
                    width="170"
                    class="rounded mr-2 mb-1 mb-md-0"
                  />
                </b-media-aside> -->
                <b-media-body>
                  <small class="text-muted">Required image resolution 350x275, max size file 1mb.</small>
                  <b-card-text class="my-50">
                    <!-- <b-link id="blog-image-text">
                      C:\fakepath\{{ blogFile ? blogFile.name : 'banner.jpg' }}
                    </b-link> -->
                  </b-card-text>
                  <div class="d-inline-block">
                    <b-form-file
                      ref="file-input"
                      :state="errors.length > 0 ? false:null"
                      accept=".jpg, .png, .gif"
                      placeholder="Choose file"
                      @change="setFileImage"
                    />
                  </div>
                </b-media-body>
                <div
                  v-if="news.file.url"
                  style="position: relative; width: 400px; height: 300px;"
                >
                  <b-img
                    :src="news.file.url"
                    rounded
                    alt="Rounded image"
                    class="d-inline-block mr-1 mt-1 w-100"
                    style="width: 375px; height: 275px;"
                  />
                  <div
                    style="position: absolute; top: 1.4rem; right: 0.5rem"
                    @click="resetImage"
                  >
                    <b-button
                      variant="gradient-danger"
                      class="btn-icon rounded-circle"
                    >
                      <feather-icon icon="XIcon" />
                    </b-button>
                  </div>
                </div>
              </b-media>
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </div>
        </b-col>
        <b-col md="12">
          <b-form-group
            label="Title"
            label-for="news-title"
            class="mb-2"
          >
            <validation-provider
              #default="{ errors }"
              name="Title"
              rules="required"
            >
              <b-form-input
                id="news-title"
                v-model="news.title"
                name="news-title"
                :state="errors.length > 0 ? false:null"
                placeholder="Type Title"
                autofocus
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </b-form-group>
        </b-col>
        <b-col md="12">
          <b-form-group
            label="Category"
            label-for="news-category"
            class="mb-2"
          >
            <b-form-select
              id="news-title"
              v-model="news.category"
              placeholder="Type Title"
              :options="options"
            />
          </b-form-group>
        </b-col>
        <b-col cols="12">
          <b-form-group
            label="Description"
            label-for="news-description"
            class="mb-4"
          >
            <input
              id="file"
              type="file"
              hidden
              @change="uploadFunction"
            >
            <validation-provider
              #default="{ errors }"
              name="Description"
              rules="required"
            >
              <quill-editor
                id="quill-editor"
                v-model="news.description"
                :options="editorOption"
                @change="oneEditorChange($event)"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </b-form-group>
        </b-col>
        <b-col
          cols="12"
          class="mt-50"
        >
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="primary"
            class="mr-1"
            @click="sendData"
          >
            Save Changes
          </b-button>
          <b-button
            v-ripple.400="'rgba(186, 191, 199, 0.15)'"
            variant="outline-secondary"
            :to="{ name: 'blog' }"
          >
            Cancel
          </b-button>
        </b-col>
      </b-row>
    </b-form>
    <b-modal
      id="crop-image"
      ref="cropimage"
      v-model="modal_crop_image"
      static
      title="Crop Image"
      @hidden="resetCropImage"
      @ok="handleCropImage"
    >
      <vue-cropper
        ref="cropper"
        :src="news.file_raw"
        :crop-box-resizable="false"
        :toggle-drag-mode-on-dblclick="false"
      />
      <b-button
        v-ripple.400="'rgba(255, 255, 255, 0.15)'"
        variant="primary"
        class="mt-1"
        @click="setCropArea"
      >
        Set Default Crop
      </b-button>
    </b-modal>
    <!--/ form -->
  </b-card>
</template>

<script>
import { ValidationProvider } from 'vee-validate'
import {
  BCard, BMedia, BCardText, BMediaBody, BForm, BRow, BCol, BFormGroup, BFormInput, BFormFile, BButton, BMediaAside, BImg, BFormSelect,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import { required } from '@validations'
import VueCropper from 'vue-cropperjs'
import 'cropperjs/dist/cropper.css'
import { quillEditor, Quill } from 'vue-quill-editor'

const AlignStyle = Quill.import('attributors/style/align')
Quill.register(AlignStyle, true)
var Block = Quill.import('blots/block');
Block.tagName = 'DIV';
Quill.register(Block, true);
const Parchment = Quill.import('parchment')

class IndentAttributor extends Parchment.Attributor.Style {
  add(node, value) {
    if (value === 0) {
      this.remove(node)
      return true
    }
    return super.add(node, `${value}em`)
  }
}

const IndentStyle = new IndentAttributor('indent', 'text-indent', {
  scope: Parchment.Scope.BLOCK,
  whitelist: ['1em', '2em', '3em', '4em', '5em', '6em', '7em', '8em', '9em'],
})
Quill.register(IndentStyle, true)

export default {
  components: {
    quillEditor,
    BCard,
    BMedia,
    BCardText,
    BMediaBody,
    BForm,
    BRow,
    BCol,
    BButton,
    BFormGroup,
    BFormInput,
    BFormFile,
    ValidationProvider,
    VueCropper,
    BMediaAside,
    BFormSelect,
    BImg,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      news: {
        title: '',
        description: '',
        image: null,
        by: '',
        required,
        file_raw: null,
        category: null,
        file: {
          url: '',
          image: null,
        },
      },
      options: [
        { value: null, text: 'Please select category' },
        { value: 'AI & DL Application Series', text: 'AI & DL Application Series' },
        { value: 'AI & DL Book Review Series', text: 'AI & DL Book Review Series' },
        { value: 'AI & DL Knowledge Series', text: 'AI & DL Knowledge Series' },
      ],
      editorOption: {
        modules: {
          toolbar: {
            container: [['bold', 'italic'], [{ align: '' }, { align: 'center' }, { align: 'right' }, { align: 'justify' }], ['link', 'image'], [{ size: ['small', false, 'large', 'huge'] }], [{ indent: '-1' }, { indent: '+1' }]],
            handlers: {
              image() {
                document.getElementById('file').click()
              },
            },
          },
        },
      },
      modal_crop_image: false,
      tag: [],
    }
  },
  methods: {
    sendData() {
      const userData = JSON.parse(localStorage.getItem('userData'))
      const formData = new FormData()
      formData.append('image', this.news.file.image)
      formData.append('title', this.news.title)
      formData.append('description', this.news.description)
      formData.append('by', userData.username)
      formData.append('category', this.news.category)
      // console.log(this.news)
      this.$http.post('blog', formData).then(response => {
        if (response.data.status === true) {
          const toaster = 'b-toaster-top-center'
          this.$bvToast.toast('Success Post', {
            title: 'Notification',
            toaster,
            variant: 'success',
            solid: false,
            appendToast: false,
          })
          setTimeout(() => {
            this.$router.push({ name: 'blog' })
          }, 2000)
        }
      })
    },
    uploadFunction(e) {
      // eslint-disable-next-line prefer-destructuring
      this.selectedFile = e.target.files[0]

      const form = new FormData()
      form.append('image', this.selectedFile)

      // upload image to server
      this.$http.post('blog/upload/image', form).then(response => {
        if (response.data.status === true) {
          // this code to set your position cursor
          const range = this.$refs.quillEdit.quill.getSelection()
          // this code to set image on your server to quill editor
          this.$refs.quillEdit.quill.insertEmbed(range.index, 'image', `${response.data.data}`)
        }
      })
    },
    runToast(type, message) {
      const toaster = 'b-toaster-top-center'
      this.$bvToast.toast(message, {
        title: 'Notification',
        toaster,
        variant: type,
        solid: false,
        appendToast: false,
      })
    },
    resetImage() {
      this.$refs['file-input'].reset()

      this.news.file.image = null
      this.news.file.url = null
      this.news.file_raw = null
    },
    setFileImage(event) {
      const file = event.target.files[0]

      if (file.type.indexOf('image/') === -1) {
        this.$refs['file-input'].reset()
        this.runToast('error', 'Please select an image file')
        return
      }

      if (typeof FileReader === 'function') {
        const reader = new FileReader()

        // eslint-disable-next-line no-shadow
        reader.onload = event => {
          // eslint-disable-next-line camelcase
          const size_file = Math.floor(file.size / 1024)

          // eslint-disable-next-line camelcase
          if (size_file <= 1024) {
            this.news.file_raw = event.target.result

            this.showModalCrop()

            // rebuild cropperjs with the updated source
            this.$refs.cropper.replace(event.target.result)
          } else {
            this.$refs['file-input'].reset()
            this.runToast('error', 'Ukuran file lebih dari 1 MB')
          }
        }
        reader.readAsDataURL(file)
      } else {
        this.$refs['file-input'].reset()
        this.runToast('error', 'Sorry, FileReader API not supported')
      }
    },
    showModalCrop() {
      this.modal_crop_image = true
    },
    resetCropImage() {
      if (!this.news.file_raw) {
        this.$refs['file-input'].reset()
      }
    },
    setCropArea() {
      this.$refs.cropper.setCropBoxData({
        width: 350,
        height: 275,
      })
    },
    handleCropImage() {
      this.news.file.url = this.$refs.cropper.getCroppedCanvas().toDataURL()
      const block = this.news.file.url.split(';')
      // eslint-disable-next-line camelcase
      const content_type = block[0].split(':')[1]
      // eslint-disable-next-line camelcase
      const real_data = block[1].split(',')[1]
      this.news.file.image = this.b64toBlob(real_data, content_type)
    },
    b64toBlob(b64Data, contentType, sliceSize) {
      // eslint-disable-next-line no-param-reassign
      contentType = contentType || ''
      // eslint-disable-next-line no-param-reassign
      sliceSize = sliceSize || 512

      const byteCharacters = atob(b64Data)
      const byteArrays = []

      for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
        const slice = byteCharacters.slice(offset, offset + sliceSize)

        const byteNumbers = new Array(slice.length)
        for (let i = 0; i < slice.length; i++) {
          byteNumbers[i] = slice.charCodeAt(i)
        }

        const byteArray = new Uint8Array(byteNumbers)

        byteArrays.push(byteArray)
      }

      const blob = new Blob(byteArrays, { type: contentType })
      return blob
    },
  },
}
</script>

